    import {  
        mapState,  
        mapMutations  
    } from 'vuex';  
    export default {  
		data() {
			const currentDate = this.getDate({format: true});
			return {
				title:'',
				price:'',
				t_limit:'',
				total:'',
				pricelist:[],
				index:0,
				logining: false,
				date: currentDate,
				eachlimit:1
			}				
		},
		computed:mapState(['sellerHasLogin']), 
		onLoad() {
			 console.log('页面加载');
			this.loadData();
		},
		
        methods: {   
            async loadData() {  
				let _this=this;
				console.log('===sellerHasLogin==='+this.sellerHasLogin);
				
                if (!_this.sellerHasLogin) {  
					uni.navigateTo({
						url:'login',
					})
                }else{
					_this.$Request.post('seller_voucher/voucher_add').then(res => {
						if (res.code == 200) {
							console.log(res);
							//this.login(res.datas);
							_this.pricelist = res.datas.price_list;
							_this.price = _this.pricelist[0];
						}
						else{
							uni.showToast({
								icon: 'none',
								title:res.datas.error,
								duration:3000,
								success: function () {
									 setTimeout(function () {
										uni.navigateTo({
											url:'/pages/seller/seller',
										});
									 }, 2000);
								}
							});
							
						}
					});
				}
            },
			formSubmit: function (e) {
				var _this =this;
				if(this.title ==''){ 
					uni.showToast({title: '请填写优惠券名称',icon:"none"});
					return false; 
				}
				if(this.price ==''){ 
					uni.showToast({title: '请填写优惠券金额',icon:"none"});
					return false; 
				}
				if(this.t_limit ==''){ 
					uni.showToast({title: '请填写优惠券消费限额',icon:"none"});
					return false; 
				}
				if(this.total ==''){ 
					uni.showToast({title: '请填写优惠券发放数量',icon:"none"});
					return false; 
				}
				if(this.eachlimit ==''){ 
					uni.showToast({title: '请填写优惠券每人限领数量',icon:"none"});
					return false; 
				}
				
				_this.logining =true;
				let postData={t_title:_this.title,t_total:_this.total,t_price:_this.price,t_limit:_this.t_limit,
						t_enddate:_this.date,t_eachlimit:_this.eachlimit};
				_this.$Request.post('seller_voucher/voucher_save',postData).then(res => {
					if (res.code == 200) {
						console.log(res);
						uni.showToast({
							icon: 'none',
							title:'添加成功！',
							duration:3000,
							success: function () {
								 setTimeout(function () {
									uni.redirectTo({
										url:'coupon',
									});
								 }, 2000);
							}
						});
						
					}
					else{
						uni.showToast({
							icon: 'none',
							title:res.datas.error,
							duration:2000,
							success: function () {
								_this.logining =false;
							}
						});
						
					}
				});
			
				
			},
			bindPickerChange: function(e) {
				console.log(e);
				this.index = e.target.value;
				this.price = this.pricelist[e.target.value];
			},
			bindDateChange: function(e) {
				this.date = e.target.value
				console.log(this.date);
			},	
			getDate(type) {
				const date = new Date();
			
				let year = date.getFullYear();
				let month = date.getMonth() + 1;
				let day = date.getDate();
			
				if (type === 'start') {
					year = year - 60;
				} else if (type === 'end') {
					year = year + 2;
				}
				month = month > 9 ? month : '0' + month;;
				day = day > 9 ? day : '0' + day;
			
				return `${year}-${month}-${day}`;
			}
			 
        }  
    }  
